/* eslint-disable */
<template>
<div id = "new-staff" style="margin-left:200px">
<h3>New Staff</h3>
<div class="row">
  <form @submit.prevent="saveStaff" class="col s12" id="newStaffForm">
    <div class="row">
      <div class="input-field col s12">
        <input type="text" v-model="staff_id" required>
        <label>Staff ID#</label>
      </div>
    </div>
    <div class="row">
      <div class="input-field col s12">
        <input type="text" v-model="staff_name" required>
        <label>Name</label>
      </div>
    </div>
    <div class="row">
      <div class="input-field col s12">
        <input type="text" v-model="staff_department" required>
        <label>Department</label>
      </div>
    </div>
    <div class="row">
      <div class="input-field col s12">
        <input type="text" v-model="staff_position" required>
        <label>Position</label>
      </div>
    </div>
    <div class="row">
      <div class="input-field col s12">
        <input type="text" v-model="staff_gender" required>
        <label>Gender</label>
      </div>
    </div>
    <div class="row">
      <div class="input-field col s12">
        <input type="text" v-model="staff_nationality" required>
        <label>Nationality</label>
      </div>
    </div>
    <div class="row">
      <div class="input-field col s12">
        <input type="text" v-model="staff_religion" >
        <label>Religion</label>
      </div>
    </div>
   <div class="row">
      <div class="input-field col s12">
        <label>Address</label>
        <textarea name="address" form="newStaffForm" rows="5" class="form-control" v-model="staff_address" required style="border-style:solid;height:100px">Enter text here...</textarea>
      </div>
    </div>
    <div class="row">
      <div class="input-field col s12">
        <input type="text" v-model="staff_tel" required>
        <label>Tel</label>
      </div>
    </div>
    <div class="row">
      <div class="input-field col s12">
        <input type="text" v-model="staff_email" >
        <label>Email</label>
      </div>
    </div>
    <div class="row">
      <div class="input-field col s12">
        <input type="text" v-model="staff_joined_date" >
        <label>Joined Date</label>
      </div>
    </div>
    <div class="row">
      <div class="input-field col s12">
        <input type="text" v-model="staff_emergency_contact" >
        <label>Emergency Contact</label>
      </div>
    </div>
    <div class="row">
      <div class="input-field col s12">
        <input type="text" v-model="staff_note" >
        <label>Note</label>
      </div>
    </div>
    <button type="submit" class="btn">Submit</button>
    <router-link to="/staff" class="btn grey">Cancel</router-link>
  </form>
</div>
</div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'new-staff',
  data () {
    return {
      staff_id: null,
      staff_name: null,
      staff_department: null,
      staff_position: null,
      staff_gender: null,
      staff_nationality: null,
      staff_religion: null,
      staff_address: null,
      staff_tel: null,
      staff_email: null,
      staff_joined_date: null,
      staff_emergency_contact: null,
      staff_note: null
    }
  },
  methods: {
    saveStaff () {
      db.collection('staff').add({
        staff_id: this.staff_id,
        staff_name: this.staff_name,
        staff_department: this.staff_department,
        staff_position: this.staff_position,
        staff_gender: this.staff_gender,
        staff_nationality: this.staff_nationality,
        staff_religion: this.staff_religion,
        staff_address: this.staff_address,
        staff_tel: this.staff_tel,
        staff_email: this.staff_email,
        staff_emergency_contact: this.staff_emergency_contact,
        staff_note: this.staff_note
      })
        .then(docref => this.$router.push('/staff'))
        // .catch(error => console.log(err))
    }
  }
}
</script>



