/* eslint-disable */
<template>
<div id = "customer-dashboard" style="margin-left:200px">
<div style="list-style-type:none" class="ul collection with-hearder">
  <li class="collection-header"><h4>Customers</h4></li>
  <li v-for="customers in customer" v-bind:key="customers.id" class="collection-item">
    {{customers.customer_id}}:{{customers.customer_name}}
    <router-link class="secondary-content" v-bind:to="{name:'view-customer', params: {customer_id: customers.customer_id}}"><i class="fa fa-eye"></i></router-link>
  </li>
  </div>
  <div class="fixed-action-btn">
    <router-link to="/newCustomer" class="btn-floating btn-large red"><i class="i fa fa-plus"></i>
    </router-link>
  </div>
</div>
  </template>
<script>
import db from './firebaseInit'
export default {
  name: 'customer-dashboard',
  data () {
    return {
      customer: []
    }
  },
  created () {
    db.collection('customer').orderBy('customer_id').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        const data = {
          'id': doc.id,
          'customer_id': doc.data().customer_id,
          'customer_name': doc.data().customer_name,
          'tel': doc.data().tel,
          'email': doc.data().email
        }
        this.customer.push(data)
      })
    })
  }
}
</script>



