<template>
  <div id="calendar" style="margin-left:200px" data-app>
    <v-container>
      <h4>Calendar</h4>
  <v-layout wrap>
    <v-flex
      xs12
      class="mb-3"
    >
      <v-sheet height="500">
        <v-calendar
          ref="calendar"
          v-model="start"
          :type="type"
          :end="end"
          color="primary"
        ></v-calendar>
      </v-sheet>
    </v-flex>

    <v-flex
      sm4
      xs12
      class="text-sm-left text-xs-center"
    >
      <v-btn @click="$refs.calendar.prev()">
        <v-icon
          dark
          left
        >
          keyboard_arrow_left
        </v-icon>
        Prev
      </v-btn>
    </v-flex>
    <v-flex
      sm2
      xs12
      class="text-xs-center"
    >
      <v-select
        v-model="type"
        :items="typeOptions"
        label="Type"
      ></v-select>
    </v-flex>
    <v-flex
      sm2
      xs12
      class="text-xs-center"
    >
      <v-menu offset-y>
      <template v-slot:activator="{ on }">
        <v-btn
          color="primary"
          dark
          v-on="on"
        >
          ADD NEW
        </v-btn>
      </template>
     <v-list>
        <v-list-tile
          v-for="add_new_option in add_new_options"
          :key="add_new_option.text"
          router :to="add_new_option.route"
        >
          <v-list-tile-title>{{ add_new_option.text }}</v-list-tile-title>
        </v-list-tile>
      </v-list>
    </v-menu>
    </v-flex>
    <v-flex
      sm4
      xs12
      class="text-sm-right text-xs-center"
    >
      <v-btn @click="$refs.calendar.next()">
        Next
        <v-icon
          right
          dark
        >
          keyboard_arrow_right
        </v-icon>
      </v-btn>
    </v-flex>
  </v-layout>
    </v-container>
    <div class="fixed-action-btn">
    <router-link to="/newBooking" class="btn-floating btn-large red"><i class="i fa fa-plus"></i>
    </router-link>
  </div>
  </div>
</template>
<script>
export default {
  data: () => ({
    type: 'month',
    start: '',
    end: '',
    typeOptions: [
      { text: 'Day', value: 'day' },
      { text: '4 Day', value: '4day' },
      { text: 'Week', value: 'week' },
      { text: 'Month', value: 'month' },
      { text: 'Custom Daily', value: 'custom-daily' },
      { text: 'Custom Weekly', value: 'custom-weekly' }
    ],
    add_new_options: [
      { text: 'New Appointment', route: './newBooking' },
      { text: 'New Blocked Time', route: './newservice' },
      {text: 'New Sale', route: './giftcard'}
    ]
  })
}
</script>



