/* eslint-disable */
<template>
<div id = "dashboard" style="margin-left:200px">
<div style="list-style-type:none" class="ul collection with-hearder">
  <li class="collection-header"><h4>Staffs</h4></li>
  <li v-for="staffs in staff" v-bind:key="staffs.id" class="collection-item">
    <div class="chip">{{staffs.staff_department}}</div>{{staffs.staff_id}}:{{staffs.staff_name}}
    <router-link class="secondary-content" v-bind:to="{name:'view-staff', params: {staff_id: staffs.staff_id}}"><i class="fa fa-eye"></i></router-link>
  </li>
</div>
<div id="calendar" data-app>
    <h1 class="teal lighten5 white--text">Calendar</h1>
  <v-layout>
    <v-flex>
      <v-sheet height="500">
        <v-calendar
          :now="today"
          :value="today"
          color="primary"
        >
          <template v-slot:day="{ present, past, date }">
            <v-layout
              fill-height
            >
              <template v-if="past && tracked[date]">
                <v-sheet
                  v-for="(percent, i) in tracked[date]"
                  :key="i"
                  :title="category[i]"
                  :color="colors[i]"
                  :width="`${percent}%`"
                  height="100%"
                  tile
                ></v-sheet>
              </template>
            </v-layout>
          </template>
        </v-calendar>
      </v-sheet>
    </v-flex>
  </v-layout>
</div>
<div class="fixed-action-btn">
    <router-link to="/newStaff" class="btn-floating btn-large red"><i class="i fa fa-plus"></i>
    </router-link>
</div>
<div class="btn-floating-action">
  <router-link to="/calendar" class="btn-floating btn-large green"><i class="i fa fa-calendar"></i>
  </router-link>
</div>
</div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'dashboard',
  data () {
    return {
      staff: [],
      today: '2019-01-10',
      tracked: {
        '2019-01-09': [23, 45, 10],
        '2019-01-08': [10],
        '2019-01-07': [0, 78, 5],
        '2019-01-06': [0, 0, 50],
        '2019-01-05': [0, 10, 23],
        '2019-01-04': [2, 90],
        '2019-01-03': [10, 32],
        '2019-01-02': [80, 10, 10],
        '2019-01-01': [20, 25, 10]
      },
      colors: ['#1867c0', '#fb8c00', '#000000'],
      category: ['Development', 'Meetings', 'Slacking']
    }
  },
  created () {
    db.collection('staff').orderBy('staff_id').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        const data = {
          'id': doc.id,
          'staff_id': doc.data().staff_id,
          'staff_name': doc.data().staff_name,
          'staff_department': doc.data().staff_department,
          'position': doc.data().position,
          'gender': doc.data().gender,
          'address': doc.data().address,
          'religion': doc.data().religion,
          'nationality': doc.data().nationality,
          'tel': doc.data().tel,
          'email': doc.data().email,
          'emergency_contact': doc.data().emergency_contact,
          'joined_date': doc.data().joined_date,
          'note': doc.data().note
        }
        this.staff.push(data)
      })
    })
  }
}
</script>



