<template>
    <v-container grid-list-xl style="margin-left:100px" data-app>
        <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
            <h4 class--primary text>New Booking</h4>
            </v-flex>
        </v-layout>
        <v-layout wrap>
            <v-flex xs12>
                <form @submit.prevent="saveBooking" >
                    <v-layout wrap>
                        <v-flex xs6 sm3 offset-sm3>
                            <!--<v-text-field flat clearable height="24px"
                                label="Name"
                                id="customer_name"
                                required>-->
                                <v-layout row>
                            <v-autocomplete
                              :items="customer_names"
                              label="Customer Name"
                              clearable
                              placeholder
                              :search-input.sync="searchInput"
                              >
                            </v-autocomplete>
                    </v-layout>
                            <!--</v-text-field>-->
                        </v-flex>
                    </v-layout>
                    <v-layout>
                        <v-flex xs6 sm3 offset-sm3>
                            <v-select
          :items="services"
          label="Service">
                            </v-select>
                        </v-flex>
                    </v-layout>
                    <v-layout row>
                        <v-flex xs6 sm3 offset-sm3 data-app>
                            <v-select
          :items="service_durations"
          label="Duration">
                            </v-select>
                        </v-flex>
                    </v-layout>
                    <v-layout row>
                      <v-flex xs6 sm3 offset-sm3>
                        <v-select
          :items="staff_names"
          label="Staff">
                            </v-select>
                      </v-flex>
                    </v-layout>
                    <v-layout row>
                      <v-flex xs6 sm3 offset-sm3>
                        <v-menu
        v-model="menu"
        :close-on-content-click="false"
        :nudge-right="40"
        lazy
        transition="scale-transition"
        offset-y
        full-width
        min-width="290px"
      >
        <template v-slot:activator="{ on }">
          <v-text-field
            v-model="date"
            label="Appointment Date"
            prepend-icon="event"
            readonly
            v-on="on"
          ></v-text-field>
        </template>
        <v-date-picker v-model="date" @input="menu = false" color="teal"></v-date-picker>
      </v-menu>
              <p>{{ date }}</p>
                      </v-flex>
                    </v-layout>
                    <v-layout row>
                      <v-flex xs6 sm3 offset-sm3>
                    <button type="submit" class="btn">Submit</button>
    <router-link to="/booking" class="btn grey">Cancel</router-link>
                      </v-flex>
                    </v-layout>
                    <v-layout row>
                      <v-flex xs6 sm3 offset-sm3>
                        <p>Start Time {{ time }}</p>
                      </v-flex>
                    </v-layout>
                </form>
            </v-flex>
        </v-layout>
    </v-container>
</template>
<script>
import db from './firebaseInit'
import moment from 'moment'
export default {
  name: 'new-booking',
  data: () => {
    return ({
      services: [],
      service_durations: [],
      menu: false,
      date: new Date().toISOString().substr(0, 10),
      customers: [],
      customer_names: [],
      staffs: [],
      staff_names: [],
      searchInput: '',
      time: new Date().toString().substr(0, 10)
    })
  },
  beforeRouteEnter (to, from, next) {
    db.collection('service').get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.id = doc.id
            vm.name = doc.data().name
            vm.service_price = doc.data().service_price
            vm.service_duration = doc.data().service_duration
            vm.service_type = doc.data().service_type
          })
        })
      })
    db.collection('customer').get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.id = doc.id
            vm.customer_name = doc.data().customer_name
          })
        })
      })
    db.collection('staff').get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.id = doc.id
            vm.staff_name = doc.data().staff_name
            vm.staff_department = doc.data().staff_department
            vm.staff_position = doc.data().staff.position
          })
        })
      })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fethData () {
      db.collection('service').get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.id = doc.id
            this.name = doc.data().name
            this.price = doc.data().price
            this.service_type = doc.data().service_type
            this.service_duration = doc.data().service_duration
            this.services.push({text: doc.data().name, value: doc.id})
            this.service_durations.push({text: doc.data().service_duration, value: doc.data().id})
          })
        })
      db.collection('customer').get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.id = doc.id
            this.customer_name = doc.data().customer_name
            this.customer_names.push({text: doc.data().customer_name, value: doc.id})
          }
          )
        })
      db.collection('staff').where('staff_department', '==', 'Service').get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.id = doc.id
            this.staff_name = doc.data().staff_name
            this.staff_department = doc.data().staff_department
            this.staff_position = doc.data().staff_position
            this.staff_names.push({text: doc.data().staff_name, value: doc.id})
          })
        })
    },
    created: function () {
      var dateTime = moment()
      this.date = dateTime.format('YYYY-MM-DD')
      this.time = dateTime.format('HH:MM')
      console.log(dateTime.format('dddd, MMMM Do YYYY'))
      this.time = dateTime.startOf('minute')
      console.log(dateTime.startOf('minute'))
    },
    saveBooking () {
      db.collection('booking').add({
        name: this.name,
        price: this.price,
        service_duration: this.service_duration,
        date: this.date,
        customer_name: this.customer_name,
        staff_name: this.staff_name,
        start_time: this.start_time
      })
        .then(docref => this.$router.push('/booking'))
        // .catch(error => console.log(err))
    }
  },
  computed: {
  },
  beforeMount () {
    this.fethData()
  }
}

</script>



