/* eslint-disable */
<template>
<div id = "new-service" style="margin-left:200px">
<h3>New Service</h3>
<div class="row">
  <form @submit.prevent="saveService" class="col s12" id="newServiceForm">
    <div class="row">
      <div class="input-field col s12">
        <input type="text" v-model="service_id" required>
        <label>Service ID#</label>
      </div>
    </div>
    <div class="row">
      <div class="input-field col s12">
        <input type="text" v-model="name" required>
        <label>Name</label>
      </div>
    </div>
    <div class="row">
      <div class="input-field col s12">
        <input type="text" v-model="price" required>
        <label>Price</label>
      </div>
    </div>
    <div class="row">
      <div class="input-field col s12">
        <label>Description</label>
        <textarea name="comment" form="newServiceForm" rows="5" class="form-control" v-model="description" required style="border-style:solid;height:100px">Enter text here...</textarea>
      </div>
    </div>
    <div class="row" data-app>
      <div class="input-area col s12">
        <v-layout row>
                        <v-flex xs12 sm6 d-flex>
                            <v-autocomplete
                            clearable
          :items="service_groups"
          label="Service Type"
          :search-input.sync="search_input_service_type">
                            </v-autocomplete>
                        </v-flex>
                    </v-layout>
      </div>
    </div>
    <div class="row">
      <div class="select col s12">
        <v-layout row data-app>
        <v-flex xs12 sm6 d-flex>
        <v-select
          :items="items"
          label="Duration"
        ></v-select>
      </v-flex>
        </v-layout>
      </div>
    </div>
    <button type="submit" class="btn">Submit</button>
    <router-link to="/service" class="btn grey">Cancel</router-link>
  </form>
</div>
</div>
</template>

<script>
import db from './firebaseInit'
import moment from 'moment'
export default {
  name: 'new-service',
  data: () => {
    return ({
      service_id: null,
      name: null,
      price: null,
      description: null,
      service_type: null,
      duration: null,
      items: ['5 mins', '10 mins', '15 mins', '20 mins', '25 mins', '30 mins', '35 mins', '40 mins', '45 mins', '50 mins', '55 mins', '1 hr', '1 hr 5 mins', '1 hr 10 mins', '1 hr 15 mins', '1 hr 20 mins'],
      service_list: [],
      service_groups: [],
      search_input_service_type: '',
      durations: []
    })
  },
  beforeRouteEnter (to, from, next) {
    db.collection('service').get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.service_price = doc.data().service_price
            vm.service_duration = doc.data().service_duration
            vm.service_type = doc.data().service_type
          })
        })
      })
    db.collection('service_group').get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.service_group_id = doc.data().service_group_id
            vm.service_group_name = doc.data().service_group_name
          })
        })
      })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fethData () {
      db.collection('service').get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.service_type = doc.data().service_type
            this.service_duration = doc.data().service_duration
          })
        })
      db.collection('service_group').get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.service_group_id = doc.data().service_group_id
            this.service_group_name = doc.data().service_group_name
            this.service_groups.push({text: doc.data().service_group_name, value: doc.data().service_group_id})
            console.log(this.service_groups.push({text: doc.data().service_group_name, value: doc.data().service_group_id}))
          })
        })
    },
    saveService () {
      db.collection('service').add({
        service_id: this.service_id,
        name: this.name,
        price: this.price,
        description: this.description,
        service_type: this.service_type,
        duration: this.duration
      })
        .then(docref => this.$router.push('/service'))
        // .catch(error => console.log(err))
    }
  },
  computed: {
    durationScale: function () {
      let scale = moment.duration(15, 'minutes')
      return scale
    }
  },
  beforeMount () {
    this.fethData()
  }
}
</script>



