/* eslint-disable */
<template>
<nav class>
  <v-toolbar app class="teal text--white">
  <v-toolbar-side-icon class="theme--dark large float" @click="drawer=!drawer"></v-toolbar-side-icon>
    <v-toolbar-title class=""><router-link to="/" class="">Spa Dashboard</router-link>
        </v-toolbar-title>
    <v-spacer></v-spacer>
    <v-btn floating flat left to="/newStaff" label="New Staff" name="New Staff" class="theme--dark hidden-md-and-down"><v-icon left>input</v-icon>New Staff
    </v-btn>
    <v-btn floating flat left to="/newCustomer" label="New Customer" name="New Customer" class="theme--dark hidden-md-and-down"><v-icon left>face</v-icon>New Customer
    </v-btn>
  </v-toolbar>
  <v-navigation-drawer app dark clipped width="200" v-model="drawer">
    <v-btn block  flat  to="/" label="home" name="home" class=""><v-icon left>home</v-icon>Home
    </v-btn>
    <v-btn block  flat  to="/staff" label="New Staff" name="New Staff" class=""><v-icon left>assignment_ind</v-icon>Staff
    </v-btn>
    <v-btn block  flat  to="/customer" label="New Customer" name="New Customer" class=""><v-icon left>people</v-icon>Customer
    </v-btn>
    <v-btn block flat  to="/calendar" label="Calendar" name="Calendar" class=""><v-icon left>calendar_today</v-icon>Calendar
    </v-btn>
     <v-btn block flat  to="/service" label="Service" name="Service" class=""><v-icon left>spa</v-icon>Service
    </v-btn>
     <v-btn block flat  to="/giftcard" label="Voucher" name="Voucher" class=""><v-icon left>card_giftcard</v-icon>Gift Card
    </v-btn> <v-btn block flat  to="/booking" label="Booking" name="Booking" class=""><v-icon left>event_available</v-icon>Booking
    </v-btn>
    <v-btn block flat  to="/store" label="Store" name="Store" class=""><v-icon left>store</v-icon>Store
    </v-btn>
    <v-btn block flat  to="/analytics" label="Analytics" name="Analytics" class=""><v-icon left>trending_up</v-icon>Analytics
    </v-btn>
    <v-btn block flat  to="/settings" label="Settings" name="Settings" class=""><v-icon left>settings</v-icon>Settings
    </v-btn>
      </v-navigation-drawer>
  </nav>
</template>
<script>
export default {
  data () {
    return {
      drawer: true
    }
  }
}
</script>



