/* eslint-disable */
<template>
<div id = "view-customer" style="margin-left:200px">
<ul class="collection with-header">
  <li class="collection-header"><h4>{{customer_name}}</h4></li>
  <li class="collectionitem">Customer ID#:{{customer_id}}</li>
  <li class="collectionitem">Tel:{{tel}}</li>
  <li class="collectionitem">Email:{{email}}</li>
</ul>
<router-link to="/customer" class="btn grey">Back</router-link>
<button @click="deleteCustomer" class="btn red">Delete</button>
<div class="fixed-action-btn">
    <router-link v-bind:to="{name: 'edit-customer', params: {customer_id}}" class="btn-floating btn-large red">
      <i class="i fa fa-pencil"></i>
    </router-link>
</div>
</div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'view-customer',
  data () {
    return {
      customer_id: null,
      customer_name: null,
      tel: null,
      email: null
    }
  },
  beforeRouteEnter (to, from, next) {
    db.collection('customer').where('customer_id', '==', to.params.customer_id).get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.customer_id = doc.data().customer_id
            vm.customer_name = doc.data().customer_name
            vm.tel = doc.data().tel
            vm.email = doc.data().email
          })
        })
      })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fethData () {
      db.collection('customer').where('customer_id', '==',
        this.$route.params.customer_id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.customer_id = doc.data().customer_id
            this.customer_name = doc.data().customer_name
            this.tel = doc.data().tel
            this.email = doc.data().email
          })
        })
    },
    deleteCustomer () {
      if (confirm('Are you sure?')) {
        db.collection('customer').where('customer_id', '==',
          this.$route.params.customer_id).get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              doc.ref.delete()
              this.$router.push('/')
            })
          })
      }
    }
  }
}
</script>



