<template>
<v-container grid-list-xl style="margin-left:100px" data app>
<div id = "service-dashboard"  xs6 sm3 offset-sm3>
  <v-flex
      sm2
      xs12
      offset-sm3
      right
      class="text-xs-center"
    >
      <v-menu offset-y>
      <template v-slot:activator="{ on }">
        <v-btn
          color="primary"
          dark
          v-on="on"
        >
          ADD NEW
        </v-btn>
      </template>
     <v-list>
        <v-list-tile
          v-for="add_new_option in add_new_options"
          :key="add_new_option.text"
          router :to="add_new_option.route"
        >
          <v-list-tile-title>{{ add_new_option.text }}</v-list-tile-title>
        </v-list-tile>
      </v-list>
    </v-menu>
    </v-flex>
    <template>
  <v-layout row justify-end>
    <v-dialog v-model="dialog" persistent max-width="600px">
      <template v-slot:activator="{ on }">
        <v-btn color="primary" dark v-on="on">New Service Group</v-btn>
      </template>
      <v-card>
        <form @submit.prevent="saveServiceGroup" class="col s12" id="newServiceGroupForm">
        <v-card-title>
          <span class="headline">New Service Group</span>
        </v-card-title>
        <v-card-text>
          <v-container grid-list-md>
            <v-layout wrap>
              <v-flex xs12 sm6 md6>
                <v-text-field v-model="service_group_name" label="Service Group Name*" required></v-text-field>
              </v-flex>
              <v-flex xs12 sm6 md6>
                <v-select
                  :items="['Red', 'Blue', 'Green', 'Yellow']"
                  label="Color*"
                  required
                  v-model="service_group_color"
                ></v-select>
              </v-flex>
              <v-flex xs12>
                <v-textarea  form="newServiceGroupForm" rows="3" class="form-control" v-model="service_group_note" label="Note*" required></v-textarea>
              </v-flex>
            </v-layout>
          </v-container>
          <small>*indicates required field</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" flat @click="dialog = false">Close</v-btn>
          <v-btn color="blue darken-1" type="submit" flat @click="dialog = false">Save</v-btn>
        </v-card-actions>
        </form>
      </v-card>
    </v-dialog>
  </v-layout>
</template>
<div style="list-style-type:none" class="ul collection with-hearder" xs6 sm3 offset-sm3>
  <li class="collection-header center" xs6 sm3 offset-sm3><h4>Service</h4></li>
  <li v-for="services in service" v-bind:key="services.id" class="collection-item" xs6 sm3 offset-sm3>
    <div class="chip">{{services.service_type}}</div>{{services.service_id}}:{{services.name}}:{{services.price}}
    <router-link class="secondary-content" v-bind:to="{name:'view-service', params: {service_id: services.service_id}}"><i class="fa fa-eye"></i></router-link>
  </li>
</div>
<div class="fixed-action-btn">
    <router-link to="/newService" class="btn-floating btn-large red"><i class="i fa fa-plus"></i>
    </router-link>
</div>
</div>
</v-container>
</template>
<script>
import db from './firebaseInit'
export default {
  name: 'service-dashboard',
  data () {
    return {
      service: [],
      add_new_options: [
        { text: 'New Service', route: './newService' },
        { text: 'New Service Group', route: './' }
      ],
      dialog: false,
      service_group_name: null,
      service_group_color: null,
      service_group_note: null
    }
  },
  methods: {
    saveServiceGroup () {
      db.collection('service_group').add({
        service_group_name: this.service_group_name,
        service_group_note: this.service_group_note,
        service_group_color: this.service_group_color
      })
        .then(docref => this.$router.push('/calendar'))
        // .catch(error => console.log(err))
    }
  },
  created () {
    db.collection('service').orderBy('service_id').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        const data = {
          'id': doc.id,
          'service_id': doc.data().service_id,
          'name': doc.data().name,
          'price': doc.data().price,
          'description': doc.data().description,
          'service_type': doc.data().service_type
        }
        this.service.push(data)
      })
    })
  }
}
</script>
<style scoped>
.theme--dark .v-menu__content {
  background-color: #424242;
}
.theme--light .v-menu__content {
  background-color: #ffffff;
}
.v-dialog .v-dialog--active .v-dialog--persistent {
 background-color: #ffffff;
}
</style>



