/* eslint-disable */
<template>
<div id = "edit-customer" style="margin-left:200px">
<h3>Edit Customers</h3>
<div class="row">
  <form @submit.prevent="updateCustomer" class="col s12">
    <div class="row">
      <div class="input-field col s12">
        <div><label>Customer ID#</label></div>
        <input disabled type="text" v-model="customer_id" required>
      </div>
    </div>
    <div class="row">
      <div class="input-field col s12">
        <div><label>Name</label></div>
        <input type="text" v-model="customer_name" required>
      </div>
    </div>
    <div class="row">
      <div class="input-field col s12">
        <div><label>Tel. No.</label></div>
        <input type="text" v-model="tel" required>
      </div>
    </div>
    <div class="row">
      <div class="input-field col s12">
        <div><label>Email</label></div>
        <input type="text" v-model="email" required>
      </div>
    </div>
    <button type="submit" class="btn">Submit</button>
    <router-link v-bind:to="{name: 'view-customer', params: {customer_id}}"  class="btn grey">Cancel</router-link>
  </form>
</div>
</div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'edit-customer',
  data () {
    return {
      customer_id: null,
      customer_name: null,
      tel: null,
      email: null
    }
  },
  beforeRouteEnter (to, from, next) {
    db.collection('customer').where('customer_id', '==', to.params.customer_id).get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.customer_id = doc.data().customer_id
            vm.customer_name = doc.data().customer_name
            vm.tel = doc.data().tel
            vm.email = doc.data().email
          })
        })
      })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fethData () {
      db.collection('customer').where('customer_id', '==',
        this.$route.params.customer_id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.customer_id = doc.data().customer_id
            this.customer_name = doc.data().customer_name
            this.tel = doc.data().ptel
            this.email = doc.data().email
          })
        })
    },
    updateCustomer () {
      db.collection('customer').where('customer_id', '==',
        this.$route.params.customer_id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref.update({
              customer_id: this.customer_id,
              customer_name: this.customer_name,
              tel: this.tel,
              email: this.email
            })
              .then(() => {
                this.$router.push({name: 'view-customer',
                  params: {customer_id: this.customer_id}})
              })
          })
        })
    }
  }
}
</script>



