/* eslint-disable */
<template>
<div id = "staff-dashboard" style="margin-left:200px">
<div style="list-style-type:none" class="ul collection with-hearder">
  <li class="collection-header"><h4>Staffs</h4></li>
  <li v-for="staffs in staff" v-bind:key="staffs.id" class="collection-item">
    <div class="chip">{{staffs.staff_department}}</div>{{staffs.staff_id}}:{{staffs.staff_name}}
    <router-link class="secondary-content" v-bind:to="{name:'view-staff', params: {staff_id: staffs.staff_id}}"><i class="fa fa-eye"></i></router-link>
  </li>
  </div>
  <div class="fixed-action-btn">
    <router-link to="/newStaff" class="btn-floating btn-large red"><i class="i fa fa-plus"></i>
    </router-link>
  </div>
</div>
  </template>
<script>
import db from './firebaseInit'
export default {
  name: 'dashboard',
  data () {
    return {
      staff: []
    }
  },
  created () {
    db.collection('staff').orderBy('staff_id').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        const data = {
          'id': doc.id,
          'staff_id': doc.data().staff_id,
          'staff_name': doc.data().staff_name,
          'staff_department': doc.data().staff_department,
          'staff_position': doc.data().staff_position,
          'staff_gender': doc.data().staff_gender,
          'staff_address': doc.data().staff_address,
          'staff_religion': doc.data().staff_religion,
          'staff_nationality': doc.data().staff_nationality,
          'staff_tel': doc.data().staff_tel,
          'staff_email': doc.data().staff_email,
          'staff_emergency_contact': doc.data().staff_emergency_contact,
          'staff_joined_date': doc.data().staff_joined_date,
          'staff_note': doc.data().staff_note
        }
        this.staff.push(data)
      })
    })
  }
}
</script>



