/* eslint-disable */
<template>
<div id = "edit-staff" style="margin-left:200px">
<h3>Edit Staff</h3>
<div class="row">
  <form @submit.prevent="updateStaff" class="col s12">
    <div class="row">
      <div class="input-field col s12">
        <div><label>Staff ID#</label></div>
        <input disabled type="text" v-model="staff_id" required>
      </div>
    </div>
    <div class="row">
      <div class="input-field col s12">
        <div><label>Name</label></div>
        <input type="text" v-model="staff_name" required>
      </div>
    </div>
    <div class="row">
      <div class="input-field col s12">
        <div><label>Department</label></div>
        <input type="text" v-model="staff_department" required>
      </div>
    </div>
    <div class="row">
      <div class="input-field col s12">
        <div><label>Position</label></div>
        <input type="text" v-model="staff_position" required>
      </div>
    </div>
     <div class="row">
      <div class="input-field col s12">
        <div><label>Gender</label></div>
        <input type="text" v-model="staff_gender" required>
      </div>
    </div>
     <div class="row">
      <div class="input-field col s12">
        <div><label>nationality</label></div>
        <input type="text" v-model="staff_nationality" required>
      </div>
    </div>
     <div class="row">
      <div class="input-field col s12">
        <div><label>Religion</label></div>
        <input type="text" v-model="staff_religion" required>
      </div>
    </div>
     <div class="row">
      <div class="input-field col s12">
        <div><label>Address</label></div>
        <input type="text" v-model="staff_address" required>
      </div>
    </div>
     <div class="row">
      <div class="input-field col s12">
        <div><label>Tel. No.</label></div>
        <input type="text" v-model="staff_tel" required>
      </div>
    </div>
     <div class="row">
      <div class="input-field col s12">
        <div><label>Email</label></div>
        <input type="text" v-model="staff_email" required>
      </div>
    </div>
     <div class="row">
      <div class="input-field col s12">
        <div><label>Joined Date</label></div>
        <input type="text" v-model="staff_joined_date" required>
      </div>
    </div>
     <div class="row">
      <div class="input-field col s12">
        <div><label>Emergency Contact</label></div>
        <input type="text" v-model="staff_emergency_contact" required>
      </div>
    </div>
     <div class="row">
      <div class="input-field col s12">
        <div><label>Note</label></div>
        <input type="text" v-model="staff_note" required>
      </div>
    </div>
    <button type="submit" class="btn">Submit</button>
    <router-link v-bind:to="{name: 'view-staff', params: {staff_id}}"  class="btn grey">Cancel</router-link>
  </form>
</div>
</div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'edit-staff',
  data () {
    return {
      staff_id: null,
      staff_name: null,
      staff_department: null,
      staff_position: null,
      staff_gender: null,
      staff_nationality: null,
      staff_religion: null,
      staff_address: null,
      staff_tel: null,
      staff_email: null,
      staff_joined_date: null,
      staff_emergency_contact: null,
      staff_note: null
    }
  },
  beforeRouteEnter (to, from, next) {
    db.collection('staff').where('staff_id', '==', to.params.staff_id).get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.staff_id = doc.data().staff_id
            vm.staff_name = doc.data().staff_name
            vm.staff_department = doc.data().staff_department
            vm.staff_position = doc.data().staff_position
            vm.staff_gender = doc.data().staff_gender
            vm.staff_nationality = doc.data().staff_nationality
            vm.staff_religion = doc.data().staff_religion
            vm.staff_address = doc.data().staff_address
            vm.staff_tel = doc.data().staff_tel
            vm.staff_email = doc.data().staff_email
            vm.staff_emergency_contact = doc.data().staff_emergency_contact
            vm.staff_note = doc.data().staff_note
          })
        })
      })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fethData () {
      db.collection('staff').where('staff_id', '==',
        this.$route.params.staff_id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.staff_id = doc.data().staff_id
            this.staff_name = doc.data().staff_name
            this.staff_department = doc.data().staff_department
            this.staff_position = doc.data().staff_position
            this.staff_gender = doc.data().staff_gender
            this.staff_nationality = doc.data().staff_nationality
            this.staff_religion = doc.data().staff_religion
            this.staff_address = doc.data().staff_address
            this.staff_tel = doc.data().staff_tel
            this.staff_email = doc.data().staff_email
            this.staff_emergency_contact = doc.data().staff_emergency_contact
            this.staff_note = doc.data().staff_note
          })
        })
    },
    updateStaff () {
      db.collection('staff').where('staff_id', '==',
        this.$route.params.staff_id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref.update({
              staff_id: this.staff_id,
              staff_name: this.staff_name,
              staff_department: this.staff_department,
              staff_position: this.staff_position,
              staff_gender: this.staff_gender,
              staff_nationality: this.staff_nationality,
              staff_religion: this.staff_religion,
              staff_address: this.staff_address,
              staff_tel: this.staff_tel,
              staff_email: this.staff_email,
              staff_emergency_contact: this.staff_emergency_contact,
              staff_note: this.staff_note
            })
              .then(() => {
                this.$router.push({name: 'view-staff',
                  params: {staff_id: this.staff_id}})
              })
          })
        })
    }
  }
}
</script>



