/* eslint-disable */
<template>
<div id = "new-customer" style="margin-left:200px">
<h3>New Customer</h3>
<div class="row">
  <form @submit.prevent="saveCustomer" class="col s12" id="newCustomerForm">
    <div class="row">
      <div class="input-field col s12">
        <input type="text" v-model="customer_id" required>
        <label>Customer ID#</label>
      </div>
    </div>
    <div class="row">
      <div class="input-field col s12">
        <input type="text" v-model="customer_name" required>
        <label>Name</label>
      </div>
    </div>
    <div class="row">
      <div class="input-field col s12">
        <input type="text" v-model="tel" required>
        <label>Tel No.</label>
      </div>
    </div>
    <div class="row">
      <div class="input-field col s12">
        <input type="text" v-model="email" required>
        <label>Email</label>
      </div>
    </div>
    <div class="row">
      <div class="input-field col s12">
        <label>Address</label>
        <textarea name="address" form="newCustomerForm" rows="5" class="form-control" v-model="address" required style="border-style:solid;height:100px">Enter text here...</textarea>
      </div>
    </div>
    <div class="row">
      <div class="input-field col s12">
        <label>Note</label>
        <textarea name="note" form="newCustomerForm" rows="5" class="form-control" v-model="note" required style="border-style:solid;height:100px">Enter text here...</textarea>
      </div>
    </div>
    <button type="submit" class="btn">Submit</button>
    <router-link to="/customer" class="btn grey">Cancel</router-link>
  </form>
</div>
</div>
</template>
<script>
import db from './firebaseInit'
export default {
  name: 'new-customer',
  data () {
    return {
      customer_id: null,
      customer_name: null,
      tel: null,
      email: null,
      address: null,
      note: null
    }
  },
  methods: {
    saveCustomer () {
      db.collection('customer').add({
        customer_id: this.customer_id,
        customer_name: this.customer_name,
        tel: this.tel,
        email: this.email,
        address: this.address,
        note: this.note
      })
        .then(docref => this.$router.push('/customer'))
        // .catch(error => console.log(err))
    }
  }
}
</script>



