/* eslint-disable */
<template>
<div id = "edit-service" style="margin-left:200px">
<h3>Edit Service</h3>
<div class="row">
  <form @submit.prevent="updateService" class="col s12">
    <div class="row">
      <div class="input-field col s12">
        <div><label>Service ID#</label></div>
        <input disabled type="text" v-model="service_id" required>
      </div>
    </div>
    <div class="row">
      <div class="input-field col s12">
        <div><label>Name</label></div>
        <input type="text" v-model="name" required>
      </div>
    </div>
    <div class="row">
      <div class="input-field col s12">
        <div><label>Price</label></div>
        <input type="text" v-model="price" required>
      </div>
    </div>
    <div class="row">
      <div class="input-field col s12">
        <div><label>Description</label></div>
        <input type="text" v-model="description" required>
      </div>
    </div>
    <div class="row">
      <div class="input-field col s12">
        <div><label>Service Type</label></div>
        <input type="text" v-model="service_type" required>
      </div>
    </div>
    <button type="submit" class="btn">Submit</button>
    <router-link v-bind:to="{name: 'view-service', params: {service_id}}"  class="btn grey">Cancel</router-link>
  </form>
</div>
</div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'edit-service',
  data: () => {
    return ({
      service_id: null,
      name: null,
      price: null,
      description: null,
      service_type: null
    })
  },
  beforeRouteEnter (to, from, next) {
    db.collection('service').where('service_id', '==', to.params.service_id).get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.service_id = doc.data().service_id
            vm.name = doc.data().name
            vm.price = doc.data().price
            vm.description = doc.data().description
            vm.service_type = doc.data().service_type
          })
        })
      })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fethData () {
      db.collection('service').where('service_id', '==',
        this.$route.params.service_id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.service_id = doc.data().service_id
            this.name = doc.data().name
            this.price = doc.data().price
            this.description = doc.data().description
            this.service_type = doc.data().service_type
          })
        })
    },
    updateService () {
      db.collection('service').where('service_id', '==',
        this.$route.params.service_id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref.update({
              service_id: this.service_id,
              name: this.name,
              price: this.price,
              description: this.description,
              service_type: this.service_type
            })
              .then(() => {
                this.$router.push({name: 'view-service',
                  params: {service_id: this.service_id}})
              })
          })
        })
    }
  }
}
</script>



