/* eslint-disable */
<template>
<div id = "view-staff" style="margin-left:200px">
<ul class="collection with-header">
  <li class="collection-header"><h4>{{staff_name}}</h4></li>
  <li class="collectionitem">Staff ID#:{{staff_id}}</li>
  <li class="collectionitem">Department:{{staff_department}}</li>
  <li class="collectionitem">Position:{{staff_position}}</li>
</ul>
<router-link to="/staff" class="btn grey">Back</router-link>
<button @click="deleteStaff" class="btn red">Delete</button>
<div class="fixed-action-btn">
    <router-link v-bind:to="{name: 'edit-staff', params: {staff_id}}" class="btn-floating btn-large red">
      <i class="i fa fa-pencil"></i>
    </router-link>
</div>
</div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'view-staff',
  data () {
    return {
      staff_id: null,
      staff_name: null,
      staff_department: null,
      staff_position: null,
      staff_gender: null,
      staff_nationality: null,
      staff_religion: null,
      staff_address: null,
      staff_tel: null,
      staff_email: null,
      staff_joined_date: null,
      staff_emergency_contact: null,
      staff_note: null
    }
  },
  beforeRouteEnter (to, from, next) {
    db.collection('staff').where('staff_id', '==', to.params.staff_id).get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.staff_id = doc.data().staff_id
            vm.staff_name = doc.data().staff_name
            vm.staff_department = doc.data().staff_department
            vm.staff_position = doc.data().staff_position
          })
        })
      })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fethData () {
      db.collection('staff').where('staff_id', '==',
        this.$route.params.staff_id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.staff_id = doc.data().staff_id
            this.staff_name = doc.data().staff_name
            this.staff_department = doc.data().staff_department
            this.staff_position = doc.data().staff_position
          })
        })
    },
    deleteStaff () {
      if (confirm('Are you sure?')) {
        db.collection('staff').where('staff_id', '==',
          this.$route.params.staff_id).get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              doc.ref.delete()
              this.$router.push('/')
            })
          })
      }
    }
  }
}
</script>



