<template>
  <div id="app">
    <NavBar />
    <div class="container">
    <router-view></router-view>
    </div>
  </div>
</template>
<script>
import NavBar from './components/NavBar'
import moment from 'moment'
export default {
  name: 'App',
  components: {
    NavBar
  },
  created: function () {
    const dateTime = moment()
    this.date = dateTime.format('dddd, Do-MMMM-YYYY').toString
    this.time = dateTime.format('HH:mm').toString
  }
}
</script>



