/* eslint-disable */
import Vue from 'vue'
import Router from 'vue-router'
import Vuetify from 'vuetify'
import 'material-design-icons-iconfont/dist/material-design-icons.css'
// index.js or main.js
import 'vuetify/dist/vuetify.min.css' // Ensure you are using css-loader
import 'vuetify/src/stylus/app.styl'
import Dashboard from '@/components/Dashboard'
import NewStaff from '@/components/NewStaff'
import ViewStaff from '@/components/ViewStaff'
import EditStaff from '@/components/EditStaff'
import StaffDashboard from '@/components/StaffDashboard'
import CustomerDashboard from '@/components/CustomerDashboard'
import NewCustomer from '@/components/NewCustomer'
import ViewCustomer from '@/components/ViewCustomer'
import EditCustomer from '@/components/EditCustomer'
import Calendar from '@/components/Calendar'
import ServiceDashboard from '@/components/Servicedashboard'
import NewService from '@/components/NewService'
import ViewService from '@/components/ViewService'
import EditService from '@/components/EditService'
import GiftCardDashboard from '@/components/GiftCardDashboard'
import BookingDashboard from '@/components/BookingDashboard'
import NewBooking from '@/components/NewBooking'
import StoreDashboard from '@/components/StoreDashboard'
import AnalyticsDashboard from '@/components/AnalyticsDashboard'
import SettingsDashboard from '@/components/SettingsDashboard'



Vue.use(Router)
Vue.use(Vuetify)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard
    },
    {
      path: '/newStaff',
      component: NewStaff
    },
    {
      path: '/viewStaff:staff_id',
      name: 'view-staff',
      component: ViewStaff
    },
    {
      path: '/edit:staff_id',
      name: 'edit-staff',
      component: EditStaff
    },
    {
      path:'/staff',
      name: 'staff-dashboard',
      component: StaffDashboard
    },
    {
      path: '/newCustomer',
      name: 'new-customer',
      component: NewCustomer
    },
    {
      path: '/viewCustomer:customer_id',
      name: 'view-customer',
      component: ViewCustomer
    },
    {
      path: '/edit:customer_id',
      name: 'edit-customer',
      component: EditCustomer
    },
    {
      path: '/customer',
      name: 'customer-dashboard',
      component: CustomerDashboard
    },
    {
    path: '/calendar',
    name: 'calendar',
    component: Calendar
    },
    {
      path: '/service',
      name: 'service-dashboard',
      component: ServiceDashboard
    },
    {
      path: '/newService',
      name: 'new-service',
      component: NewService
    },
    {
      path: '/viewService:service_id',
      name: 'view-service',
      component: ViewService
    },
    {
      path: '/editService:service_id',
      name: 'edit-service',
      component: EditService
    },
    {
      path: '/giftcard',
      name: 'giftcard-dashboard',
      component: GiftCardDashboard
    },
    {
      path: '/booking',
      name: 'booking-dashboard',
      component: BookingDashboard
    },
    {
      path: '/newBooking',
      name: 'new-booking',
      component: NewBooking
    },
    {
      path: '/store',
      name: 'store-dashboard',
      component: StoreDashboard
    },
    {
      path: '/analytics',
      name: 'analytics-dashboard',
      component: AnalyticsDashboard
    },
    {path: '/settings',
    name: 'settings-dashboard',
    component: SettingsDashboard
  }
  ]
})



