/* eslint-disable */
<template>
<div id = "view-service" style="margin-left:200px">
<ul class="collection with-header">
  <li class="collection-header"><h4>{{name}}</h4></li>
  <li class="collectionitem">Service ID#:{{staff_id}}</li>
  <li class="collectionitem">Price:{{price}}</li>
  <li class="collectionitem">Description:{{description}}</li>
  <li class="colectionitem">Service Type:{{service_type}}</li>
</ul>
<router-link to="/service" class="btn grey">Back</router-link>
<button @click="deleteService" class="btn red">Delete</button>
<div class="fixed-action-btn">
    <router-link v-bind:to="{name: 'edit-service', params: {service_id}}" class="btn-floating btn-large red">
      <i class="i fa fa-pencil"></i>
    </router-link>
</div>
</div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'view-service',
  data () {
    return {
      service_id: null,
      name: null,
      price: null,
      description: null,
      service_type: null
    }
  },
  beforeRouteEnter (to, from, next) {
    db.collection('service').where('service_id', '==', to.params.service_id).get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.service_id = doc.data().service_id
            vm.name = doc.data().name
            vm.price = doc.data().price
            vm.description = doc.data().description
            vm.service_type = doc.data().service_type
          })
        })
      })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fethData () {
      db.collection('service').where('service_id', '==',
        this.$route.params.service_id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.service_id = doc.data().service_id
            this.name = doc.data().name
            this.price = doc.data().price
            this.description = doc.data().description
            this.service_type = doc.data().service_type
          })
        })
    },
    deleteService () {
      if (confirm('Are you sure?')) {
        db.collection('service').where('service_id', '==',
          this.$route.params.service_id).get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              doc.ref.delete()
              this.$router.push('/')
            })
          })
      }
    }
  }
}
</script>



